import type { FloorCardConfig } from "types";

export const configValidator = (config: Partial<FloorCardConfig>): void => {
  if (config.show_floor_icons !== undefined) {
    if (
      typeof config.show_floor_icons !== "boolean" &&
      config.show_floor_icons !== "always"
    ) {
      throw new Error("Invalid show_floor_icons value");
    }
  }
  if (config.floor_icons_position !== undefined) {
    if (
      typeof config.floor_icons_position !== "string" ||
      (config.floor_icons_position !== "left" &&
        config.floor_icons_position !== "right")
    ) {
      throw new Error("Invalid floor_icons_position value");
    }
  }
  if (config.show_area_icons !== undefined) {
    if (
      typeof config.show_area_icons !== "boolean" &&
      config.show_area_icons !== "always"
    ) {
      throw new Error("Invalid show_area_icons value");
    }
  }
  if (config.area_icons_position !== undefined) {
    if (
      typeof config.area_icons_position !== "string" ||
      (config.area_icons_position !== "left" &&
        config.area_icons_position !== "right")
    ) {
      throw new Error("Invalid area_icons_position value");
    }
  }
  if (config.entity_icon_placement !== undefined) {
    if (
      typeof config.entity_icon_placement !== "string" ||
      (config.entity_icon_placement !== "left" &&
        config.entity_icon_placement !== "right")
    ) {
      throw new Error("Invalid entity_icon_placement value");
    }
  }
  if (config.domain_sort_order !== undefined) {
    if (!Array.isArray(config.domain_sort_order)) {
      throw new Error("Invalid domain_sort_order value");
    }
    for (const domain of config.domain_sort_order) {
      if (typeof domain !== "string") {
        throw new Error("Invalid domain_sort_order value");
      }
    }
  }
  if (config.class_sort_order !== undefined) {
    if (!Array.isArray(config.class_sort_order)) {
      throw new Error("Invalid class_sort_order value");
    }
    for (const className of config.class_sort_order) {
      if (typeof className !== "string") {
        throw new Error("Invalid class_sort_order value");
      }
    }
  }
  if (config.include_domains !== undefined) {
    if (!Array.isArray(config.include_domains)) {
      throw new Error("Invalid include_domains value");
    }
    for (const domain of config.include_domains) {
      if (typeof domain !== "string") {
        throw new Error("Invalid include_domains value");
      }
    }
  }
  if (config.include_classes !== undefined) {
    if (!Array.isArray(config.include_classes)) {
      throw new Error("Invalid include_classes value");
    }
    for (const className of config.include_classes) {
      if (typeof className !== "string") {
        throw new Error("Invalid include_classes value");
      }
    }
  }
  if (config.include_states !== undefined) {
    if (!Array.isArray(config.include_states)) {
      throw new Error("Invalid include_states value");
    }
    for (const state of config.include_states) {
      if (typeof state !== "string") {
        throw new Error("Invalid include_states value");
      }
    }
  }
  if (config.include !== undefined) {
    if (typeof config.include !== "object") {
      throw new Error("Invalid include value");
    }
  }
};
